import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import firebaseApp from '../firebaseConfig';


const useStyles = makeStyles({
    table: {
      minWidth: 650,
    },
  });
  
  function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
  }
  
  const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
  ];

class AdminMain extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            data: {},
            submissions: []
        }
    }

    componentDidMount(){
        const submissionsRef = firebaseApp.database().ref('submissions');
        const votersRef = firebaseApp.database().ref('voters');

        var dataBuild = {};

        submissionsRef.on('value', (snapshot) => {
            let submissions = snapshot.val();
            var dataBuild = {};

            for (let submission in submissions){
                dataBuild[submission] = {
                    votes: [],
                    title: submissions[submission].title,
                    description: submissions[submission].description,
                    url: submissions[submission].url,
                    firstName: submissions[submission].firstName,
                    lastName: submissions[submission].lastName,
                    id: submission
                };

                votersRef.on('value', (snap) => {
                    let allVoters = snap.val();

                    for (let voter in allVoters){
                        var voterRef = firebaseApp.database().ref(`voters/${voter}/votes`);

                        voterRef.on('value', (snp) => {
                            let votes = snp.val();
                            if (submission in votes){ //If this submission has been voted by voter
                                const voteValue = votes[submission];
                                dataBuild[submission].votes.push(voteValue);
                            }
                            
                        });
                    }
                });
                this.setState({
                    data: dataBuild
                });
            }

            var submissionsArr = [];
            Object.keys(this.state.data).forEach(submission => {
                submissionsArr.push(this.state.data[submission])
            });   
            this.setState({
                submissions: submissionsArr
            });
        });

        
    }

    render(){
        
        
        return (
            <TableContainer component={Paper}>
              <Table  aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell><b>Title</b></TableCell>
                    <TableCell align="right"><b>URL</b></TableCell>
                    <TableCell align="right"><b>Description</b></TableCell>
                    <TableCell align="right"><b>First Name</b></TableCell>
                    <TableCell align="right"><b>Last Name</b></TableCell>
                    <TableCell align="right"><b>Total Votes</b></TableCell>
                    <TableCell align="right"><b>Average Rating</b></TableCell>
                    <TableCell align="right"><b>Median Rating</b></TableCell>

                  </TableRow>
                </TableHead>
                <TableBody>


                  {this.state.submissions.map(submission => (
                    <TableRow key={submission.id}>
                      <TableCell component="th" scope="row">
                        {submission.title}
                      </TableCell>
                      <TableCell align="right">
                          <a href={submission.url} target="_blank">
                            <img  src={submission.url} width="auto" height="50"/>
                           </a>
                      </TableCell>
                      <TableCell align="right">{submission.description}</TableCell>
                      <TableCell align="right">{submission.firstName}</TableCell>
                      <TableCell align="right">{submission.lastName}</TableCell>

                    </TableRow>
                  ))}
                </TableBody>
              </Table>
              <button onClick={() => {console.log(this.state)}}>Get the fuckin state</button>

            </TableContainer>
          );
    }

}
export default AdminMain;