import React from 'react';
import { Spinner, Col, Button, Form, FormGroup, Label, Input, Container, CustomInput } from 'reactstrap';
import firebaseApp from '../firebaseConfig';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { AvForm, AvField } from 'availity-reactstrap-validation';
import SubmissionSuccess from './SubmissionSuccess';


const initialState = {
    firstName: '',
    lastName: '',
    netId: '',
    grade: '',
    title: '',
    medium: '',
    year: '',
    description: '',
    image: null,
    url: '',
    dimensions: '',
    submitted: false,
    loading: false
};


class SubmissionForm extends React.Component {

  constructor(props) {
    super(props);
    
    this.state = initialState;


    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleFiles = this.handleFiles.bind(this);
  }

resetState(){
    this.setState(initialState);
}

  handleChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    
    this.setState({
        [name]: value
    });
  }

  handleFiles(event){
      if (event.target.files[0]){
          const img = event.target.files[0];
          this.setState({
            image: img
          });
      }
  }

  validateInput(){

  }

  handleSubmit(event) {
    event.preventDefault();
    this.setState({loading: true});

    /** Submit File to Firebase Storage and submission to database*/
    var file = this.state.image;
    var storageRef = firebaseApp.storage().ref(`Spring2020/${file.name}`);

    storageRef.put(file).then(() => {
        storageRef.getDownloadURL().then(urlResult => {
            this.setState({url: urlResult});
            this.pushToDatabase();
            
            console.log('Uploaded file successfully: ', file.name, this.state);
            this.setState({
                submitted: true
            });
        });
    })    

    /**ADD THIS TO SOMEWHERE NOT HERE LMAO */
    //this.resetState();


  }

  pushToDatabase(){
    /** Submit text data to Firebase real-time database*/
    var submissionsRef = firebaseApp.database().ref('submissions');
    var submission = {
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        netId: this.state.netId,
        grade: this.state.grade,
        title: this.state.title,
        medium: this.state.medium,
        year: this.state.year,
        dimensions: this.state.dimensions,
        description: this.state.description,
        url: this.state.url
    };
    submissionsRef.push(submission);
  }

  render() {
    return (
        
        <div className="submissionForm">
            {
                (!this.state.submitted) ?
            <Form onSubmit={this.handleSubmit} className="form">

                <FormGroup >
                    <Label>First Name</Label>
                    <Input name="firstName" type="text" placeholder="First Name" value={this.state.firstName} onChange={this.handleChange} />
                </FormGroup>

                <FormGroup >
                    <Label>Last Name</Label>
                    <Input name="lastName" type="text" placeholder="Last Name" value={this.state.lastName} onChange={this.handleChange} />
                </FormGroup>
    
                <FormGroup >
                    <Label>NetID</Label>
                    <Input name="netId" type="text" placeholder="NetID" value={this.state.netId} onChange={this.handleChange} />
                </FormGroup>

                <FormGroup >
                    <Label>Current grade level</Label>
                    <Input name="grade" type="select" value={this.state.grade} onChange={this.handleChange} placeholder=''>
                        <option></option>
                        <option>First-year</option>
                        <option>Sophomore</option>
                        <option>Junior</option>
                        <option>Senior</option>
                        <option>Graduate/professional</option>
                    </Input>
                </FormGroup>

                <FormGroup>
                    <Label>Upload artwork</Label>
                    <CustomInput type="file" name="file" onChange={this.handleFiles} />
                </FormGroup>


                <FormGroup >
                    <Label>Title of work</Label>
                    <Input name="title" type="text" placeholder="Title" value={this.state.title} onChange={this.handleChange} />
                </FormGroup>

                <FormGroup >
                    <Label>Description</Label>
                    <Input name="description" type="textarea" placeholder="Description" value={this.state.description} onChange={this.handleChange} />
                </FormGroup>

                <FormGroup >
                    <Label>Medium</Label>
                    <Input name="medium" type="text" placeholder="e.g. digital, screenprint, watercolor, etc." value={this.state.medium} onChange={this.handleChange} />
                </FormGroup>

                <FormGroup >
                    <Label>Dimensions (width x height)</Label>
                    <Input name="dimensions" type="text" placeholder="e.g. 10 in. x 16 in." value={this.state.dimensions} onChange={this.handleChange} />
                </FormGroup>
        
                <FormGroup >
                    <Label>Year of creation</Label>
                    <Input name="year" type="year" placeholder="Year" value={this.state.year} onChange={this.handleChange} />
                </FormGroup>

                


                <Button color="primary" type="submit">
                    Submit
                </Button>
                {
                    (this.state.loading) ? 
                    <Spinner color="primary" /> : ''
                }
            </Form>
            :
            <SubmissionSuccess name={this.state.firstName}/>
            }
        </div>
        
    );
  }
}

export default SubmissionForm;
